import { game } from 'js/main';
import { loadUserData } from 'js/sdk/loadUserData';
import * as PIXI from 'pixi.js';

export class Background extends PIXI.Sprite {
  constructor() {
    super();
    let bg = "bg-basic"
    loadUserData(['shopState']).then(data => {
      if (data.shopState) {
        const saved = data.shopState.backgrounds.find(b => b.status == "selected")
        if (saved) {
          bg = saved.key
        }
      }
      this.updateImage(bg);
    })
  }

  updateImage(key: string) {
    if (key == "bg-basic") {
      this.texture = PIXI.Texture.WHITE;
      this.tint = 0x320481;
    } else {
      this.texture = PIXI.Texture.from(key);
      this.tint = 0xFFFFFF;
    }

    this.handleResize([game.screen.width, game.screen.height]);
  }

  public handleResize([width, height]: [number, number]): void {
    // stretch to fill all available space, maintaining image aspect ratio
    const xScale = width / this.texture.baseTexture.width;
    const yScale = height / this.texture.baseTexture.height;
    const maxScale = Math.max(xScale, yScale)
    this.scale.set(maxScale);
    // keep the image centered
    this.position.set((width - this.width) / 2, (height - this.height) / 2)
  }
}
