type Filter = Partial<{
  root: boolean;
	titlePanel: boolean;
	titleLabel: boolean;
  description: boolean;
  adButton: boolean;
  adButtonIcon: boolean;
  adButtonLabel: boolean;
  closeButton: boolean;
}>;

class SecondChancePopupLayout {
  public readonly root: PIXI.Container;
	public readonly titlePanel: PIXI.Sprite;
	public readonly tileLabel: PIXI.Text;
  public readonly description: PIXI.Text;
  public readonly adButton: PIXI.Sprite;
  public readonly adButtonIcon: PIXI.Sprite;
  public readonly adButtonLabel: PIXI.Sprite;
  public readonly closeButton: PIXI.Sprite;

  constructor(filter?: Filter) {
    populate(this, raw, filter);
  }
}

export { SecondChancePopupLayout, Filter, raw };