export class VerticalScroll {
  hitTarget: PIXI.Container
  moveTarget: PIXI.Container
  dragContainer: PIXI.DisplayObject
  initialPos: number
  dragOffset: number
  scrollWindowHeight: number
  isDragging: boolean = false

  constructor(hitTarget: PIXI.Container, scrollWindowHeight: number, moveTarget: PIXI.Container, dragContainer: PIXI.DisplayObject) {
    this.hitTarget = hitTarget;
    this.moveTarget = moveTarget
    this.hitTarget.interactive = true;
    this.dragContainer = dragContainer;
    this.dragContainer.interactive = true;
    this.scrollWindowHeight = scrollWindowHeight

    this.initialPos = moveTarget.position.y;
    this.hitTarget.on('pointerdown', this.onDragStart, this);
    this.dragContainer.on('pointerup', this.onDragEnd, this);
    this.dragContainer.on('pointerupoutside', this.onDragEnd, this);
  }

  onDragStart(e: PIXI.InteractionEvent) {
    this.isDragging = true
    this.dragContainer.on('pointermove', this.onDragMove, this);
    this.dragOffset = this.moveTarget.position.y - this.getLocalPos(e);
  }

  onDragMove(event: PIXI.InteractionEvent) {
    if (this.isDragging) {
      const newY = this.dragOffset + this.getLocalPos(event);
      const l = this.moveTarget.height - this.scrollWindowHeight;
      const boundTop = Math.max(newY, this.initialPos - l);
      this.moveTarget.position.y = Math.min(boundTop, 0);
    }
  }

  onDragEnd() {
    if (this.isDragging) {
      this.dragContainer.off('pointermove', this.onDragMove);
      this.isDragging = false;
    }
  }

  getLocalPos(e: PIXI.InteractionEvent): number {
    return this.moveTarget.parent.toLocal(e.data.global).y;
  }
}