import { TweenManager } from "js/framework/TweenManager/TweenManager";
import { VerticalScroll } from "js/framework/utils/VerticalScroll";
import { LBEntryLayout } from "js/layouts/LBEntryLayout";
import { LBLayout } from "js/layouts/LBLayout";
import { LeaderboardLayout } from "js/layouts/LeaderboardLayout";
import { game } from "js/main";
import { getLeaderboardEntries, setPlayerEntry } from "js/sdk/ysdk-lb";
import { Rectangle } from "pixi.js";

type IScore = {
  name: string
  score: number
  // localUser: boolean
}

interface ILeaderboardViewConfig {
  parent: PIXI.Container;
  tweenMgr: TweenManager;
  score: number;
  onclose: () => void;
}

export class LeaderboardPopupV2 {
  private readonly tweenMgr: TweenManager;
  private layout: LBLayout;

  constructor({
    parent,
    tweenMgr,
    score,
    onclose
  }: ILeaderboardViewConfig) {
    this.tweenMgr = tweenMgr;

    const leaderboardData: IScore[] = [ // mock data
      // { name: "Alice", score: 9000 },
      // { name: "Bob", score: 8000 },
      // { name: "Carol", score: 7000 },
      // { name: "Dan", score: 6000 },
      // { name: "Erin", score: 5000 },
      // { name: "Player 6", score: 4000 },
      // { name: "Player 7", score: 3000 },
      // { name: "Player 8", score: 2000 },
      // { name: "Player 9", score: 1000 },
      // { name: "Player 10", score: 900 },
      // { name: "Player 11", score: 800 },
      // { name: "Player 12", score: 700 },
    ]

    getLeaderboardEntries("score").then(rankings => {
      console.log("Rankings", rankings)
      if (rankings) {
      rankings.entries.forEach((e, i) => {
        this.addEntry(e.rank, e.player.publicName || "Anonymous player", e.score)
      });

      this.addPlayerEntry(rankings.userRank, score)
      }
    }).catch(() => {
      const playerEntry = {name: game.getLocalized("You"), score: score}
      leaderboardData.push(playerEntry)
      leaderboardData.sort((a, b) => b.score - a.score)
      leaderboardData.forEach((e, i) => {
        this.addEntry(i+1, e.name, e.score)
      });
      this.addPlayerEntry(leaderboardData.indexOf(playerEntry)+1, score);
    }).finally(() => {

      const maskRect = new PIXI.Rectangle(
        -this.layout.bg.width/2 + 50, -this.layout.bg.height/2 + 170,
        this.layout.bg.width - 100, this.layout.bg.height - 300
      );

      const entries = this.layout.ranksMaskContainer;
      entries.mask = new PIXI.Graphics().drawRect(
          maskRect.x, maskRect.y, maskRect.width, maskRect.height
        );
      entries.hitArea = maskRect;
      entries.addChild(entries.mask);

      new VerticalScroll(entries, maskRect.height - 100, this.layout.ranksContainer, this.layout.root);
      this.showPopup();
    });

    this.layout = new LBLayout();
    parent.addChild(this.layout.root);

    this.layout.closeButton.on('pointerdown', () => {
      onclose();
      this.hidePopup();
    })
  }

  private updateEntryData(entry: LBEntryLayout, place: number, name: string, score: number) {
    entry.rankLabel.text = place.toString();
    entry.nameLabel.text = name;
    entry.scoreValue.text = score.toString();
  }

  private addPlayerEntry(place: number, score: number) {
    const entry = new LBEntryLayout();
    entry.mainBg.texture = PIXI.Texture.from('box-you');
    entry.rankBg.texture = PIXI.Texture.from('rank-box-you');
    entry.rankBg.position.x -= 20;
    entry.rankLabel.style.strokeThickness = 0;

    this.updateEntryData(entry, place, game.getLocalized("You"), score);

    this.layout.playerEntry.addChild(entry.root);
  }

  private addEntry(place: number, name: string, score: number) {
    const entry = new LBEntryLayout();
    entry.root.position.set(0, place * 150 - 660);
    entry.medal.texture = PIXI.Texture.from(`medal-${place}`);

    this.updateEntryData(entry, place, name, score)

    switch(place) {
      case 1:
        entry.mainBg.texture = PIXI.Texture.from('box-1');
        entry.rankBg.texture = PIXI.Texture.from('rank-box-1');
        entry.rankLabel.style.stroke = "#B9461D";
        entry.nameLabel.style.fill = "#D05300";
        entry.scoreText.style.fill = "#D05300";
        entry.scoreValue.style.fill = "#D05300";
        break;
      case 2:
        entry.mainBg.texture = PIXI.Texture.from('box-2');
        entry.rankBg.texture = PIXI.Texture.from('rank-box-2');
        entry.rankLabel.style.stroke = "#34525B";
        entry.nameLabel.style.fill = "#008500";
        entry.scoreText.style.fill = "#008500";
        entry.scoreValue.style.fill = "#008500";
        break;
      case 3:
        entry.mainBg.texture = PIXI.Texture.from('box-3');
        entry.rankBg.texture = PIXI.Texture.from('rank-box-3');
        entry.rankLabel.style.stroke = "#630F0E";
        entry.nameLabel.style.fill = "#820CD1";
        entry.scoreText.style.fill = "#820CD1";
        entry.scoreValue.style.fill = "#820CD1";
        break;
    }

    this.layout.ranksContainer.addChild(entry.root);
  }

  private showPopup() {
    this.tweenMgr.create(this.layout.root)
    .from({alpha: 0}, 300, this.tweenMgr.easing.Sinusoidal.Out, true);
  }

  private async hidePopup() {
    await this.tweenMgr.create(this.layout.root)
      .to({alpha: 0}, 400, this.tweenMgr.easing.Sinusoidal.Out, true)
      .asyncOnComplete();

    this.layout.root.destroy();
  }
}
