type Filter = Partial<{
  root: boolean;
  bg: boolean;
  titlePanel: boolean;
  titleLabel: boolean;
  bubbleSetOptions: boolean;
  bubbleSetOptionsLabel: boolean;
  bubbleSetOptionButtons: boolean;

  musicLabelOn: boolean;
  continueButton: boolean;
  restartButton: boolean;
  leaderboardButton: boolean;
  closeButton: boolean;
}>;

class ShopLayout {
  public readonly root: PIXI.Container;
  public readonly bg: PIXI.Sprite;
  public readonly titlePanel: PIXI.Sprite;
  public readonly titleLabel: PIXI.Text;
  public readonly bubbleSetOptions: PIXI.Container;
  public readonly bubbleSetOptionsLabel: PIXI.Text;
  public readonly bubbleSetOptionButtons: PIXI.Container;
  public readonly bgOptions: PIXI.Container;
  public readonly bgOptionsLabel: PIXI.Text;
  public readonly bgOptionButtons: PIXI.Container;
  public readonly closeButton: PIXI.Sprite;

  constructor(filter?: Filter) {
    populate(this, raw, filter);
  }
}

export { ShopLayout, Filter, raw };