import * as PIXI from 'pixi.js';
import { EventType, IHasEventHandlers, EventHandlers } from 'js/bubbleCore/events';
import { Lerper } from 'framework/utils/Lerper';
import { Range } from 'framework/utils/Range';
import { ControlBarLayout } from 'js/layouts/ControlBarLayout';

export class ControlBarView extends PIXI.Container implements IHasEventHandlers {
  public readonly eventHandlers: EventHandlers = {
    [EventType.ChainChanged]: (e) => this.layout.comboValue.text = `x${e.data.newValue}`,
    [EventType.IncreasedScore]: (e) => this.scoreLerper.setTarget(e.data.amount),
  };

  private readonly layout: ControlBarLayout;
  private readonly scoreLerper: Lerper;
  public readonly shopButton: PIXI.Sprite;
  public readonly settingsButton: PIXI.Sprite;

  constructor() {
    super();
    this.layout = new ControlBarLayout();
    this.addChild(this.layout.root);

    this.settingsButton = this.layout.settingsButton;
    this.shopButton = this.layout.shopButton;

    this.scoreLerper = new Lerper({
      initValue: 0,
      lerp: 0.2,
      snap: 0.5,
      autoUpdate: true,
      range: new Range(0, Infinity),
      destroySignal: this.layout.scoreValue.onDestroyed,
    });

    this.scoreLerper.signals.onValueUpdate.add((value) => {
      this.layout.scoreValue.text = Math.round(value).toString();
    });
  }

  public disableButtons(): void {
    this.layout.shopButton.interactive = false;
    this.layout.settingsButton.interactive = false;
  }

  public enableButtons(): void {
    this.layout.shopButton.interactive = true;
    this.layout.settingsButton.interactive = true;
  }
}