import * as PIXI from 'pixi.js';
import { TweenManager } from 'framework/TweenManager/TweenManager';
import { LeaderboardPopupV2 } from './LeaderboardPopupV2';
import { SettingsPopupLayout } from 'js/layouts/SettingsPopupLayout';
import { BubbleGameBoard } from 'js/bubbleCore/BubbleGameBoard';
import { BubbleGameBoardView } from 'js/bubbleCore/view/BubbleGameBoardView';
import { MuteReason, SoundManager } from 'js/framework/SoundManager/SoundManager';
import { saveUserData } from 'js/sdk/saveUserData';
import { loadUserData } from 'js/sdk/loadUserData';

interface SettingsPopupConfig {
  parent: PIXI.Container;
  tweenMgr: TweenManager;
  board: BubbleGameBoard;
  view: BubbleGameBoardView;
  soundManager: SoundManager;
  onclose: () => void;
  onrestart: () => void;
}

export class SettingsPopup {
  private readonly layout: SettingsPopupLayout;
  private readonly tweenMgr: TweenManager;
  private readonly board: BubbleGameBoard;
  private view: BubbleGameBoardView

  constructor({
    parent,
    board,
    view,
    tweenMgr,
    onclose,
    onrestart,
    soundManager
  }: SettingsPopupConfig) {
    this.tweenMgr = tweenMgr;
    this.board = board;
    this.view = view;
    this.layout = new SettingsPopupLayout();
    parent.addChild(this.layout.root);

    const {
      closeButton,
      continueButton,
      restartButton,
      leaderboardButton,
    } = this.layout;

    closeButton.once("pointerdown", () => {
      soundManager.play("button_click");
      onclose();
      this.hidePopup();
    });

    continueButton.once("pointerdown", () => {
      soundManager.play("button_click");
      onclose();
      this.hidePopup();
    });

    restartButton.once("pointerdown", () => {
      soundManager.play("button_click");
      onrestart();
    });

    leaderboardButton.on("pointerdown", () => {
      soundManager.play("button_click");
      this.showLeaderboard();
    });

    const refreshSfxButton = () => {
      this.layout.soundLabelOn.visible = soundManager.getSoundEnabled();
      this.layout.soundLabelOff.visible = !soundManager.getSoundEnabled();
    };

    const refreshMusicButton = () => {
      this.layout.musicLabelOn.visible = soundManager.getMusicEnabled();
      this.layout.musicLabelOff.visible = !soundManager.getMusicEnabled();
    };

    this.layout.soundToggle.interactive = true;
    this.layout.soundToggle.on("pointerdown", async () => {
      console.log("Sound toggle click");
      soundManager.play("button_click");
      soundManager.setSoundEnabled(!soundManager.getSoundEnabled(), MuteReason.UserAction);
      refreshSfxButton();
      try {
        saveUserData({soundEnabled: soundManager.getSoundEnabled()});
      } catch (e) {
        console.warn(e);
      }
    });

    this.layout.musicToggle.interactive = true;
    this.layout.musicToggle.on("pointerdown", async () => {
      console.log("Music toggle click");
      soundManager.play("button_click");
      soundManager.setMusicEnabled(!soundManager.getMusicEnabled(), MuteReason.UserAction);
      refreshMusicButton();
      try {
        saveUserData({musicEnabled: soundManager.getMusicEnabled()});
      } catch (e) {
        console.warn(e);
      }
    });

    refreshSfxButton();
    refreshMusicButton();

    this.showPopup();
  }

  private async showLeaderboard() {
    const bestScore = (await loadUserData(['highscore'])).highscore || 0;
    new LeaderboardPopupV2({
      parent: this.layout.root.parent,
      tweenMgr: this.tweenMgr,
      score: bestScore,
      onclose: () => {}
    });
  }

  private async showPopup() {
    this.tweenMgr.create(this.layout.root)
      .from({alpha: 0}, 300, this.tweenMgr.easing.Sinusoidal.Out, true);
  }

  public async hidePopup() {
    await this.tweenMgr.create(this.layout.root)
      .to({alpha: 0}, 400, this.tweenMgr.easing.Sinusoidal.Out, true)
      .asyncOnComplete();

    this.layout.root.destroy();
  }
}
