import * as PIXI from 'pixi.js';
import { TweenManager } from 'framework/TweenManager/TweenManager';
import { BubbleGameBoard } from 'js/bubbleCore/BubbleGameBoard';
import { BubbleGameBoardView } from 'js/bubbleCore/view/BubbleGameBoardView';
import { SoundManager } from 'js/framework/SoundManager/SoundManager';
import { ShopLayout } from 'js/layouts/ShopLayout';
import { ShopItemLayout } from 'js/layouts/ShopItemLayout';
import { loadUserData } from 'js/sdk/loadUserData';
import { ShopItemStatus, ShopItemView } from 'js/bubbleCore/view/ShopItemView';
import { saveUserData } from 'js/sdk/saveUserData';

type ThemeUpdateHandler = (e: {type: "bg" | "bubbles", key: string}) => void

interface ShopPopupConfig {
  parent: PIXI.Container;
  tweenMgr: TweenManager;
  soundManager: SoundManager;
  board: BubbleGameBoard;
  view: BubbleGameBoardView;
  onclose: () => void;
  onthemeupdate: ThemeUpdateHandler;
}

interface IShopItemState {
  key: string
  status: ShopItemStatus
}

interface IShopState {
  bubbles: IShopItemState[]
  backgrounds: IShopItemState[]
}

const initialState: IShopState = {
  bubbles: [
    {
      key: "bubbles-animal",
      status: "offered"
    },
    {
      key: "bubbles-basic",
      status: "selected"
    },
    {
      key: "bubbles-candy",
      status: "offered"
    },
  ],
  backgrounds: [
    {
      key: "bg-animal",
      status: "offered"
    },
    {
      key: "bg-basic",
      status: "selected"
    },
    {
      key: "bg-candy",
      status: "offered"
    },
  ]
}

export class ShopPopup {
  private readonly layout: ShopLayout;
  private readonly tweenMgr: TweenManager;
  private readonly board: BubbleGameBoard;
  private view: BubbleGameBoardView
  private bubblesGroup: ShopItemView[] = [];
  private backgroundsGroup: ShopItemView[] = [];
  private handler: ThemeUpdateHandler;

  constructor({
    parent,
    tweenMgr,
    soundManager,
    board,
    view,
    onclose,
    onthemeupdate,
  }: ShopPopupConfig) {
    this.tweenMgr = tweenMgr;
    this.board = board;
    this.view = view;
    this.layout = new ShopLayout();
    parent.addChild(this.layout.root);

    this.handler = onthemeupdate;

    const {
      closeButton,
    } = this.layout;

    loadUserData(["shopState"]).then(state => {
      console.log("shop state", state);
      this.initShopState(state.shopState || initialState)
    })

    closeButton.once("pointerdown", () => {
      soundManager.play("button_click");
      onclose();
      this.hidePopup();
    });

    this.showPopup();
  }

  private initShopState(shopState: IShopState) {
    const gap = 275;

    shopState.bubbles.forEach((s, i) => {
      const item = new ShopItemView(s.status, "bubbles", s.key, (target: ShopItemView) => {
        this.updateItemsStatus(this.bubblesGroup, target)
      });
      item.position.set((i - 1) * gap, 0);
      this.layout.bubbleSetOptionButtons.addChild(item);
      this.bubblesGroup.push(item);
    });

    shopState.backgrounds.forEach((s, i) => {
      const item = new ShopItemView(s.status, "bg", s.key, (target: ShopItemView) => {
        this.updateItemsStatus(this.backgroundsGroup, target)
      });
      item.position.set((i - 1) * gap, 0);
      this.layout.bgOptionButtons.addChild(item);
      this.backgroundsGroup.push(item);
    });
  }

  private updateItemsStatus(group: ShopItemView[], target: ShopItemView) {
    switch (target.currentStatus) {
      case 'offered':
        target.setStatus("purchased");
        break;
      case 'purchased':
        target.setStatus("selected");
        this.handler({type: target.type, key: target.key})
        group.forEach(b => {
          if (b.currentStatus == "selected" && b != target) {
            b.setStatus("purchased");
          }
        });
        break;
      case 'selected':
        break;
    }

    saveUserData({shopState: {
      bubbles: this.bubblesGroup.map(i => { return ({key: i.key, status: i.currentStatus})}),
      backgrounds: this.backgroundsGroup.map(i => { return ({key: i.key, status: i.currentStatus})})
    }});
  }

  private async showPopup() {
    this.tweenMgr.create(this.layout.root)
      .from({alpha: 0}, 300, this.tweenMgr.easing.Sinusoidal.Out, true);
  }

  public async hidePopup() {
    await this.tweenMgr.create(this.layout.root)
      .to({alpha: 0}, 400, this.tweenMgr.easing.Sinusoidal.Out, true)
      .asyncOnComplete();

    this.layout.root.destroy();
  }
}
