import { ShopItemLayout } from "js/layouts/ShopItemLayout";
import { game } from "js/main";

const statuses = ["offered", "purchased", "selected"] as const;
export type ShopItemStatus = "offered" | "purchased" | "selected"

export class ShopItemView extends PIXI.Container {
  layout: ShopItemLayout
  currentStatus: ShopItemStatus
  type: "bg" | "bubbles"
  key: string

  constructor(status: ShopItemStatus, type: "bg" | "bubbles", key: string, onpress: any) {
    super();
    this.type = type;
    this.key = key;
    this.layout = new ShopItemLayout();
    this.addChild(this.layout.root);

    this.initData(key);
    this.setStatus(status);

    this.layout.buttonContainer.interactive = true;

    this.layout.buttonContainer.on("pointerup", () => {
      onpress(this);
    });
  }

  initData(key: string) {
    this.layout.thumbnail.texture = PIXI.Texture.from(`item-${key}`);
  }

  setStatus(status: ShopItemStatus) {
    switch (status) {
      case "offered":
        this.layout.buttonBg.texture = PIXI.Texture.from('btn-green-mid');
        this.layout.buttonLabel.text = game.getLocalized("Free");
        this.layout.buttonLabel.style.fill = "white";
        this.layout.buttonLabel.style.stroke = "#1E5900";
        this.layout.buttonLabel.style.strokeThickness = 4;
        break;
      case "purchased":
        this.layout.buttonBg.texture = PIXI.Texture.from('btn-orange-mid');
        this.layout.buttonLabel.text = game.getLocalized("Select");
        this.layout.buttonLabel.style.fill = "white";
        this.layout.buttonLabel.style.stroke = "#984900";
        this.layout.buttonLabel.style.strokeThickness = 4;
        break;
      case "selected":
        this.layout.buttonBg.texture = PIXI.Texture.from('btn-mid-selected');
        this.layout.buttonLabel.text = game.getLocalized("Selected");
        this.layout.buttonLabel.style.fill = "#1B369C"
        this.layout.buttonLabel.style.strokeThickness = 0;
        break;
    }
    this.currentStatus = status;
  }
}