type Filter = Partial<{
  root: boolean;
	shadow: boolean;
	background: boolean;
  infoPanel: boolean;
  infoPanelSeparator: boolean;
  comboContainer: boolean;
  comboLabel: boolean;
  comboValue: boolean;
  scoreContainer: boolean;
  scoreLabel: boolean;
  scoreValue: boolean;
  settingsButton: boolean;
  settingsButtonIcon: boolean;
  shopButton: boolean;
  shopButtonIcon: boolean;
}>;

class ControlBarLayout {
  public readonly root: PIXI.Container;
	public readonly shadow: PIXI.Sprite;
  public readonly background: PIXI.Sprite;
  public readonly infoPanel: PIXI.Sprite;
  public readonly infoPanelSeparator: PIXI.Sprite;
  public readonly comboContainer: PIXI.Container;
  public readonly comboLabel: PIXI.Text;
  public readonly comboValue: PIXI.Text;
  public readonly scoreContainer: PIXI.Container;
  public readonly scoreLabel: PIXI.Text;
  public readonly scoreValue: PIXI.Text;
  public readonly settingsButton: PIXI.Sprite;
  public readonly settingsButtonIcon: PIXI.Sprite;
  public readonly shopButton: PIXI.Sprite;
  public readonly shopButtonIcon: PIXI.Sprite;

  constructor(filter?: Filter) {
    populate(this, raw, filter);
  }
}

export { ControlBarLayout, Filter, raw };